<template>
  <div id="app">
    <loading :active.sync="isLoading"></loading>
    <router-view/>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'App',
  computed:{
    ...mapGetters(['isLoading'])
  }
}
</script>

<style lang='scss'>
@import '@/assets/scss/all.scss';

.SourceLight{
  font-family: 'Noto Sans TC',serif;
  font-weight: 500;
  font-style: normal;
}
#app{
  list-style: none;
  width: 100%;
  height: 100%;
  font-family: 'Noto Sans TC',serif;
  font-weight: 900;
  font-style: normal;
}
label{
  font-weight: 300;
}
</style>



