import axios from 'axios'
import moment from 'moment';

export default{
    strict:true,
    namespaced:true,
    state:{
        like:[],
        cart:{},
        alreadyAddToCart:false,
        totalPrice:0,
        planformfee:100
    },
    actions:{
        //讀取心願單裡的項目
        getLike(context){
            const getlike = JSON.parse(localStorage.getItem('LIKE')) || [];
            if(getlike.length == 0) return; //沒有就別讀了
            context.commit('GETLIKE',getlike); //更新心願但
            context.commit('TOTALPRICE'); //更新心願單裡的總價錢
        },
        //新增心願
        //payload = { startDate , endDate , product , product_id , Price , originPrice , qty , isLike }
        addToLike(context,payload){
            context.commit('ADDTOLIKE',payload); //把新的心願push到心願單
            context.commit('TOTALPRICE'); //更新心願單裡的總價錢
        },
        //刪除心願
        DelLike(context,payload){
            //如果有心願單才執行
            if(context.state.like.length > 0){
                let elementPos = context.state.like.map(function(x) {return x['product_id']; }).indexOf(payload); //選定心願index位置
                //有此心願才執行刪除動作
                if(elementPos != -1){
                    context.commit('DISLIKE',elementPos); //刪除更新心願單
                    localStorage.setItem('LIKE', JSON.stringify(context.state.like)); //存到localStorage
                    context.commit('TOTALPRICE',payload); //更新心願單裡的總價錢
                }else{
                    alert('沒有此產品'); //無此心願不會執行刪除動作
                }
            }
        },
        //更新心願單裡的項目值
        updateLikeItem(context,{ data , index }){
            let day = moment.duration(moment(data.end, 'YYYY/MM/DD').diff(moment(data.start, 'YYYY/MM/DD'))).asDays(); //更新後的天數
            let newTotalPrice = context.state.like[index].originPrice * day; //更新後的單一心願總價
            context.commit('UPDATEITEMDATE',{ data , index }); //更新項目日期
            context.commit('UPDATEITEMTOTALPRICE', { newTotalPrice , index }); //更新項目總價
            context.commit('TOTALPRICE'); //更新心願單裡的總價錢
        },
        async getcart(context){
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            context.commit('LOADING',true,{root:true});
            await axios.get(url).then((response) =>{
                context.commit('LOADING',false,{root:true});
                context.commit('CART',response.data.data)
            });
        },
        async addtocart(context,payload){
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            context.commit('LOADING',true,{root:true});
            console.log(payload)
            await axios.post(url,{data:payload}).then((response) =>{
                console.log('addtocart',response.data);
                context.commit('ALREADYADDTOCART',true)
                context.commit('LOADING',false,{root:true}); 
            });
        },
        async delCart(context,payload){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${payload}`;
            context.commit('LOADING',true,{root:true});
            await axios.delete(api).then((response) =>{
                console.log(response.data)
                context.commit('LOADING',false,{root:true});
                context.dispatch('getcart')
            });
        },
    },
    mutations:{
        // 讀取心願單裡的項目
        GETLIKE(state,payload){
            state.like = payload
        },
        // 新增心願
        ADDTOLIKE(state,payload){
            state.like.push(payload);
        },
        //按讚icon顏色
        ISLIKE(state,{ elementPos , boolean }){
            if(elementPos === -1) return false;
            state.like[elementPos].isLike = boolean;
        },
        // 刪除心願
        DISLIKE(state,payload){
            state.like.splice(payload,1)
        },
        //更新項目日期
        UPDATEITEMDATE(state,{ data , index }){
            state.like[index].startDate = data.start;
            state.like[index].endDate = data.end;
        },
        //更新項目總價
        UPDATEITEMTOTALPRICE(state,{ newTotalPrice , index }){
            state.like[index].Price = newTotalPrice;
        },
        //心願單全部總價
        TOTALPRICE(state){
            state.totalPrice = Object.values(state.like).reduce((t, {Price}) => t + Price, 0)
        },
        CART(state,payload){
            state.cart = payload;
        },
        ALREADYADDTOCART(state,payload){
            state.alreadyAddToCart = payload;
        }
    },
    getters:{
        like: state => state.like,
        cart: state => state.cart,
        alreadyAddToCart: state => state.alreadyAddToCart,
        totalPrice: state => state.totalPrice,
        planformfee: state => state.planformfee
    }
}


