<template>
    <div class="product-container d-sm-flex flex-sm-wrap col-12 p-1 p-sm-0 mt-4">
        <!-- 電腦版 -->
        <div class="product-form-desktop position-relative mb-4" v-if="isLoading === false">
            <!-- 選單 -->
            <form class="product-form-desktop-DormSearchEngine d-flex flex-column flex-wrap position-absolute p-1 bg-white outer-inset-shadow">
                    <!-- 目的地 -->
                    <div class="form-group w-100 mb-1">
                        <label for="destination" class="text-primary">您要去哪呢?</label>
                        <input id="destination" placeholder="目的地" v-model="destination" type="text" label="destination" name="destination" class="form-control mt-md-0 bg-white border text-primary SourceLight round-circle">
                    </div>
                    <!-- 日期 -->
                    <div class="form-group w-100 mb-1">
                        <label for="Datepicker" class="text-primary w-100">入住/退房</label>
                        <VueHotelDatepicker
                        id="Datepicker"
                        class="product-form-desktop-Datepicker w-100 round-circle"
                        :isRound="true"
                        :maxDate="maxDate"
                        :startDate="startDate"
                        :endDate="endDate"
                        :placeholder="today"
                        @confirm="updateSelectDate"
                        :mobile="'mobile'" />
                    </div>
                    <!-- 人數 -->
                    <div class="form-group mb-1">
                        <label for="guest" class="text-primary">客人數</label>
                        <div id="guest" class="input-group">
                            <div class="input-group-prepend">
                                <button class="btn btn-primary text-white round-circle" @click="updateGuest(1)">+</button>
                            </div>
                            <div id="guestNum" class="form-control text-center">{{guest}}</div>
                            <div class="input-group-append">
                                <button class="btn btn-primary text-white round-circle" @click="updateGuest(-1)">-</button>
                            </div>
                        </div>
                    </div>
                    <div class="form-group">
                        <label for="DormType" class="text-primary">房型</label>
                        <select name="DormType" id="" class="product-form-desktop-DormSearchEngine-DormTypeSlector text-primary custom-select round-circle" v-model="Dormtype">
                            <option value="經典雙人房">經典雙人房</option>
                            <option value="悠閒單人房">悠閒單人房</option>
                            <option value="歡樂混合房">歡樂混合房</option>
                            <option value="野生搭帳">野生搭帳</option>
                        </select>
                    </div>
                <!-- 表單送出按鈕 -->
                <!-- <button type="submit" class="btn btn-primary col-md w-100 font-weight-bold align-self-end">送出</button> -->
            </form>
            
            <GmapMap
            :center="mapCenter"
            :zoom="12"
            map-type-id="roadmap"
            :options="{
                zoomControl: true,
                mapTypeControl: false,
                scaleControl: false,
                streetViewControl: false,
                rotateControl: false,
                fullscreenControl: false,
                disableDefaultUi: false,
            }"
            style="width: 100%; height: 450px"
            >
            <gmap-info-window  
            :options="infoOptions" 
            :position="infoWindowPos" 
            :opened="infoWinOpen"
            @closeclick="infoWinOpen=false">
            </gmap-info-window>

            <GmapMarker
                :key="index"
                v-for="(m, index) in position"
                :position="m.positionPoint"
                :clickable="true"
                :draggable="true"
                @click="mapCenter = m.positionPoint;toggleInfoWindow(m,index)"
            />
            </GmapMap>
        </div>
        <!-- 手機板 -->
        <div class="product-form-mobile">
            <!-- 選單 -->
            <form class="product-form-mobile-DormSearchEngine d-flex flex-row flex-wrap bg-white p-2">
                <!-- 目的地 -->
                <div class="w-100 mb-1 form-group">
                    <label for="destination-mobile" class="text-primary">您要去哪裡呢?</label>
                    <input id="destination-mobile" placeholder="目的地" v-model="destination" type="text" name="destination" class="p-4 form-control SourceLight text-primary bg-white border round-circle shadow-sm">
                </div>
                <!-- 更新日期按鈕 -->
                <button @click="openDatePages" type="button" class="col align-self-end btn btn-primary font-weight-bold round-circle">日期</button>
                <!-- 更新人數&房型按鈕 -->
                <button @click="openOtherFilterPages" type="button" class="col align-self-end btn btn-primary font-weight-bold round-circle">人數和房型</button>
                <!-- 日期 -->
                <div class="product-form-mobile-DormSearchEngine-DatePages d-none sildeOut position-fixed vw-100 bg-white">
                    <i @click="openDatePages" class="fas fa-times text-primary"></i>
                    <MobileHotelDatePicker
                        class="product-form-mobile-DormSearchEngine-Datepicker"
                        :maxDate="maxDate"
                        :startDate="startDate"
                        :endDate="endDate"
                        :placeholder="today"
                        @confirm="updateSelectDate"
                        :ParentMethod="openDatePages"
                        :mobile="'mobile'" />
                </div>
                <!-- 人數&房型 -->
                <div class="product-form-mobile-DormSearchEngine-OtherFilterPages d-none sildeOut position-fixed vw-100 bg-white">
                    <i @click="openOtherFilterPages" class="align-self-end fas fa-times text-primary"></i>
                    <div class="mb-1 p-2 form-group">
                        <label for="guest-mobile" class="text-primary h4 pb-2">客人數</label>
                        <div class="d-flex flex-row align-items-center justify-content-between input-group">
                            <label for="guest-mobile" class="col-3 m-0 text-primary h5">人數</label>
                            <div class="d-flex flex-row align-items-center justify-content-between col-6 input-group">
                                <button type="button" class="m-0 btn btn-primary text-white circle" @click="updateGuest(1)">
                                    <plusSign class="plus"/>
                                </button>
                                <span>{{guest}}</span>
                                <button type="button" class="m-0 btn btn-primary text-white circle" @click="updateGuest(-1)">
                                    <subtractSign class="subtract"/>
                                </button>
                            </div>
                        </div>
                    </div>
                    <div class="form-group mb-1 p-2">
                        <label for="DormType-mobile" class="text-primary h4 pb-2">房型</label>
                        <select name="DormType-mobile" class="product-form-mobile-DormSearchEngine-DormTypeSlector text-primary h6" v-model="Dormtype">
                            <option value="經典雙人房">經典雙人房</option>
                            <option value="悠閒單人房">悠閒單人房</option>
                            <option value="歡樂混合房">歡樂混合房</option>
                            <option value="野生搭帳">野生搭帳</option>
                        </select>   
                    </div>
                    <button @click="openOtherFilterPages" type="button" class="float-right w-75 mt-7 mr-3 btn btn-primary font-weight-bold">送出</button>
                </div>
            </form>
            <button type="button" class="product-form-mobile-maker btn btn-white round-circle bg-white position-fixed">
                <i @click="openMapPages" class="fas fa-map-marker-alt text-primary"></i>
            </button>
            <div class="product-form-mobile-MapPages d-none sildeOut vw-100 position-fixed bg-white">
                <i @click="openMapPages" class="fas fa-times position-absolute text-primary"></i>
                <GmapMap
                :center="mapCenter"
                :zoom="12"
                map-type-id="roadmap"
                :options="{
                    zoomControl: true,
                    mapTypeControl: false,
                    scaleControl: false,
                    streetViewControl: false,
                    rotateControl: false,
                    fullscreenControl: false,
                    disableDefaultUi: false,
                    gestureHandling: 'greedy'
                }"
                style="width: 100%; height: 100%"
                >
                <gmap-info-window  
                :options="infoOptions" 
                :position="infoWindowPos" 
                :opened="infoWinOpen"
                @closeclick="infoWinOpen=false">
                </gmap-info-window>

                <GmapMarker
                    :key="index"
                    v-for="(m, index) in position"
                    :position="m.positionPoint"
                    :clickable="true"
                    :draggable="true"
                    @click="mapCenter = m.positionPoint;toggleInfoWindow(m,index)"
                />
                </GmapMap>
            </div>
        </div>

        <div class="w-100 products-content" v-if="isLoading === false">
            <!-- 產品列 -->
            <div v-for="(item,index) in FilterProducts" :key="item.id" class="product-item d-flex flex-row flex-wrap w-100 p-1 mb-4 shadow">
                <!-- 輪播 -->
                <div :id="`carouselControls${index}`" class="product-item-carouselControls carousel slide col-sm-5 col-12" data-ride="carousel" data-interval="false">
                    <div class="carousel-inner rounded">
                        <!-- 圖一 -->
                        <div class="carousel-item active overflow-hidden">
                            <div class="product-item-carouselControls-img d-block" :style="{backgroundImage:`url(${item.imageUrl})`}" style="background-position: center;background-size: 100% 100%;"></div>
                        </div>
                        <!-- 圖二 -->
                        <div class="carousel-item overflow-hidden">
                            <div class="product-item-carouselControls-img d-block" :style="{backgroundImage:`url(${item.imageUrl1})`}" style="background-position: center;background-size: 100% 100%;"></div>
                        </div>
                        <!-- 圖三 -->
                        <div class="carousel-item overflow-hidden">
                            <div class="product-item-carouselControls-img d-block" :style="{backgroundImage:`url(${item.imageUrl2})`}" style="background-position: center;background-size: 100% 100%;"></div>
                        </div>
                    </div>
                    <!-- 換頁按鈕-左 -->
                    <a class="carousel-control-prev" :href="`#carouselControls${index}`" role="button" data-slide="prev">
                        <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                        <span class="sr-only">Previous</span>
                    </a>
                    <!-- 換頁按鈕-右 -->
                    <a class="carousel-control-next" :href="`#carouselControls${index}`" role="button" data-slide="next">
                        <span class="carousel-control-next-icon" aria-hidden="true"></span>
                        <span class="sr-only">Next</span>
                    </a>
                </div>

                <div class="d-flex flex-column col-12 col-sm-7">
                    <!-- 房間類型 -->
                    <span class="mt-1 mt-md-0 badge badge-forth SourceLight" style="width:60%">{{item.content}}</span>
                    <!-- 住宿名稱 -->
                    <router-link :to="{path: `/tempproduct/${item.id}`}" class="product-item-title mt-1 h5 text-primary">{{item.title}}</router-link>
                    <!-- 地址 -->
                    <small class="SourceLight text-secondary">
                        {{addressText(item)}}
                    </small>
                    <!-- 基本介紹 -->
                    <small class="SourceLight">
                        {{item.description}}
                    </small>
                    <!-- 價位、房間數 -->
                    <div class="d-flex flex-row flex-wrap align-items-end justify-content-between mt-4 mt-sm-4 mt-md-3 mt-lg-8">
                        <div class="col-12 col-sm order-sm-1 p-0 products-price">
                            <!-- 房間數 -->
                            <div class="mb-1 text-muted text-right SourceLight">共 {{item.unit}} 間</div>
                            <!-- 價位 -->
                            <div class="p-0 mb-0 text-right h5 text-primary">$ {{item.origin_price}} TWD / 晚</div>
                            <small class="text-muted float-right SourceLight">總金額$ {{item.price}} TWD</small>
                        </div>
                        <!-- 評價 -->
                        <div class="col-12 col-sm order-sm-0 p-sm-0 products-star text-right text-sm-left">
                            <!-- 星星 -->
                            <i v-for="num in 5" :key="num" class="fas fa-star p-1 text-warning"></i>
                            <span class="ml-1 SourceLight">120</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery';
//Vues mapGetters
import { mapGetters } from 'vuex';
//Datepicker
import VueHotelDatepicker from '@northwalker/vue-hotel-datepicker/src/components/VueHotelDatepicker.vue'
import MobileHotelDatePicker from '@/components/MobileHotelDatePicker.vue'
//symobl
import plusSign from '@/components/icon/plusSign'
import subtractSign from '@/components/icon/subtractSign'
//moment.js 處理日期
import moment from 'moment';

export default {
    data(){
        return{
            infoWindowPos: null,
            infoWinOpen: false,
            currentMidx: null,
            infoOptions: {
                content: '',
                pixelOffset: {
                    width: 0,
                    height: -35
                }
            },
        };
    },
    components:{
        VueHotelDatepicker,
        MobileHotelDatePicker,
        plusSign,
        subtractSign
    },
    watch:{
    },
    computed: {
        // 過濾產品，選擇器
        FilterProducts(){
            const vm = this;
            //有條件下
            if(vm.destination || vm.Dormtype){
                //篩選地址
                let destinationFilterData = this.$store.state.productModules.products.filter(item =>{
                    return item.address.autocompleteText.toLowerCase().includes(vm.destination.toLowerCase());
                })
                //再篩選房型
                let DormtypeFilterData = destinationFilterData.filter((item)=>{
                    return item.category === vm.Dormtype;
                })
                //取得座標
                this.$store.dispatch('productModules/getPosition',DormtypeFilterData);
                //更新地圖中心座標
                this.$store.dispatch('productModules/updateMapCenter');
                return DormtypeFilterData;
            }
            //無條件下，顯示全部
            this.$store.dispatch('productModules/getPosition',this.$store.state.productModules.products)
            return this.$store.state.productModules.products;
        },
        //大地圖中心位置，因為非同步所以位置還沒回傳時，中心先設為 {lat:24,lng:24}
        mapCenter:{
            get(){
                return this.$store.state.productModules.mapCenter;
            },
            set(val){
                return this.$store.commit('productModules/MAPCENTER',val);
            }
        },
        //更新目的地(地址)
        destination:{
            get(){
                return this.$store.state.searchEngine.destination;
            },
            set(destination){
                this.$store.commit('searchEngine/DESTINATION',destination);
            }
        },
        //更新房型
        Dormtype:{
            get(){
                return this.$store.state.searchEngine.Dormtype;
            },
            set(type){
                this.$store.commit('searchEngine/DORMTYPE',type);
                //更動房型時才更動地圖中心位置
                this.$store.dispatch('productModules/updateMapCenter');
            }
        },
        ...mapGetters(['isLoading']),
        ...mapGetters('searchEngine',['today','maxDate','guest','startDate','endDate']),
        ...mapGetters('productModules',['products','totalPage','position'])
    },
    methods:{
        //取得所有房
        getProducts(){
            this.$store.dispatch('productModules/getProducts');
        },
        //給列表顯示地址
        addressText(item){
            if(item.address != undefined){
                let autocompleteText = item.address.autocompleteText == undefined ? '' : item.address.autocompleteText;
                return `${autocompleteText}`
            }else{
                return "未設定地址"
            }
        },
        //更新日期，提供計算天數
        updateSelectDate(data){
            this.$store.commit('searchEngine/STARTDATE',data.start);
            this.$store.commit('searchEngine/ENDDATE',data.end);
        },
        //更新人數
        updateGuest(num){
            this.$store.dispatch('searchEngine/updateGuestNum',num)
        },
        //顯示地圖資訊頁面
        toggleInfoWindow: function(marker, idx) {
            this.infoWindowPos = marker.positionPoint;
            this.infoOptions.content = marker.infoText;
            //check if its the same marker that was selected if yes toggle
            if (this.currentMidx == idx) {
              this.infoWinOpen = !this.infoWinOpen;
            }
            //if different marker set infowindow to open and reset current marker index
            else {
              this.infoWinOpen = true;
              this.currentMidx = idx;
            }
        },
        openDatePages(){
            $('.product-form-mobile-DormSearchEngine-DatePages')
            .toggleClass('sildeOut sildeIn')
            .delay(800)
            .toggleClass('d-none')
            .css('height',`${window.outerHeight}`);

            $('.product-container')
            .toggleClass('stop-scorll');
        },
        openOtherFilterPages(){
            $('.product-form-mobile-DormSearchEngine-OtherFilterPages')
            .toggleClass('sildeOut sildeIn')
            .delay(800)
            .toggleClass('d-none')
            .css('height',`${window.outerHeight}`);

            $('.product-container')
            .toggleClass('stop-scorll');
        },
        openMapPages(){
            $('.product-form-mobile-MapPages')
            .toggleClass('sildeOut sildeIn')
            .delay(800)
            .toggleClass('d-none')
            .css('height',`${window.outerHeight}`);

            $('.product-container')
            .toggleClass('stop-scorll');
        }
    },
    mounted() {
    },
    created(){
        this.getProducts();
    },
}
</script>

<style lang="scss">
@keyframes fadein {
    0%{
        transform: translateY(10%);
        opacity:0 ;
    }
    100%{
        transform: translateY(0%);
        opacity: 1;
    }
}
@keyframes sildeIn {
    from{
        transform: translateY(0%);
        opacity:0 ;
    }
    to{
        transform: translateY(-800px);
    }
}
@keyframes sildeOut {
    from{
        transform: translateY(-800px);
    }
    to{
        transform: translateY(0%);
        opacity:0 ;
    }
}
a{
    cursor: pointer;
}
a:hover{
    color:lighter(#3F5D45,20%);
}
.stop-scorll{
    overflow: hidden;
    position: fixed;
}
.sildeIn{
    animation: sildeIn 0.5s cubic-bezier(0, 0, 0, 1.01);
    transform:translateY(-800px) !important; 
}
.sildeOut{
    animation: sildeOut 0.5s cubic-bezier(1.01, 0, 0, 0);
    transform:translateY(0) !important; 
}
</style>

<style lang="scss" scoped>
@import '@/assets/scss/_breakpoint.scss';

.fa-star{
        width: 5% !important;
}



.product-container{
    min-height: 800px;

    //電腦搜尋
    .product-form-desktop{
        width: 100%;
        max-width: 100%;
        height: auto;
        display: block;
        @include breakpoint(xs){
            display: none;
        }
        &-DormSearchEngine{
            width: 25%;
            z-index: 10;
            top: 10%;
            left: 2%;
        }  
    }

    //手機搜尋
    .product-form-mobile{
        display: none;
        @include breakpoint(xs){
            display: block;
            button{
                margin: 3px;
            }
            &-DormSearchEngine{
                &-Datepicker{
                    width: 100%;
                    height: 420px;
                }
                &-DatePages,&-OtherFilterPages{
                    z-index:110;
                    top:800px;
                    left:0;
                    overflow: hidden;
                    transition: .5s cubic-bezier(0.075, 0.82, 0.165, 1);
                }
                &-DormTypeSlector{
                    -moz-appearance: none;
                    -webkit-appearance: none;
                    appearance: none;
                    width: 100%;
                    border: none !important;
                    border-bottom:thin solid #3F5D45 !important;
                    padding: 0.375rem 1.75rem 0.8rem 0.75rem;
                    &::-ms-expand{
                        display: none;
                    }
                    &:focus{
                        outline-offset: 0 !important;
                        outline:none;
                    }
                }
                .fa-times{
                    font-size: 2rem;
                    padding: 1rem;
                    right: 0;
                }
            }
            &-MapPages{
                z-index:110;
                top:800px;
                left:0;
                transition: .5s cubic-bezier(0.075, 0.82, 0.165, 1);
            }
            &-maker{
                width: 60px;
                height: 60px;
                border-radius: 50%;
                box-shadow: 0 5px 6px rgba(0, 0, 0, 0.3), 0 -.5px 2px rgba(0, 0, 0, 0.3);
                z-index: 100;
                right: 20px;
                bottom: 20px;
                .fa-map-marker-alt{
                    font-size: 1.5rem;
                }
            }
            .fa-times{
                z-index: 120;
                font-size: 2rem;
                padding: 1rem;
                right: 0;
            }
        }
    }

    //產品列
    .products-content{
        @include breakpoint(xs){
            overflow-y: scroll;
        }
        .product-item{
            max-height: 350px;
            transition: .2s ease-out;
            &:hover,&:active{
                transform: translateY(-10px);
                box-shadow: 0 1rem 1rem rgba(black, .2) !important;
            }
            @include breakpoint(xs){
                min-height: 570px !important;
            }
            &-carouselControls{
                &-img{
                    min-height: 252px;
                }
                @include breakpoint(xs){
                    min-height: 252px;
                }
            }
            &-title{
                @include breakpoint(xs){
                    font-size: 1.5rem !important;
                }
            }
            .fa-star{
                font-size: 10px !important;
                padding-left: 6px !important;
                padding-right: 6px !important;
            }
        }
    }
}

</style>


