<template>
    <div class="w-100">
        <!-- topimage -->
        <div class="top-image d-flex d-flex flex-md-row flex-wrap align-items-center col-12 mt-6">
            <!-- 點腦版選單 -->
            <div class="top-image-form-desktop d-none d-sm-block col-md-6 col-lg-5">
                <form @submit.prevent="PushToDormitoryPage" id="DormSearchEngine-desktop" class="top-image-DormSearchEngine d-flex flex-column flex-wrap p-2 bg-success rounded">
                    <h3 class="text-primary">在Snailing上尋找您的住處</h3>
                    <h6 class="mb-2 text-primary SourceLight">發掘適合你的各種獨立套房。</h6>
                    <!-- 目的地 -->
                    <div class="w-100 form-group mb-1">
                        <label for="destination" class="text-primary">您要去哪呢?</label>
                        <input placeholder="目的地" v-model="destination" type="text" name="destination" class="form-control bg-white border text-primary SourceLight round-circle mt-md-0">
                    </div>
                    <!-- 日期 -->
                    <div class="w-100 form-group mb-1">
                        <label for="Datepicker" class="text-primary">入住/退房</label>
                        <VueHotelDatepicker
                        class="w-100 round-circle"
                        :isRound="true"
                        :maxDate="maxDate"
                        :startDate="startDate"
                        :endDate="endDate"
                        :placeholder="today"
                        @confirm="updateSelectDate"
                        :mobile="'mobile'" />
                    </div>
                    <!-- 人數 -->
                    <div class="mb-1 form-group">
                        <label for="guest" class="text-primary">客人數</label>
                        <div class="input-group">
                            <div class="input-group-prepend">
                                <button type="button" @click="updateGuest(1)" class="btn btn-primary text-white round-circle">+</button>
                            </div>
                            <div id="adultNum" class="form-control text-center">{{guest}}</div>
                            <div class="input-group-append">
                                <button type="button" @click="updateGuest(-1)" class="btn btn-primary text-white round-circle">-</button>
                            </div>
                        </div>
                    </div>
                    <div class="form-group">
                        <label for="Dormtype" class="text-primary">房型</label>
                        <select v-model="Dormtype" name="DormType" class="text-primary custom-select round-circle">
                            <option value="經典雙人房" selected>經典雙人房</option>
                            <option value="悠閒單人房">悠閒單人房</option>
                            <option value="歡樂混房">歡樂混房</option>
                            <option value="野生搭帳">野生搭帳</option>
                        </select>
                    </div>
                    <!-- 表單送出按鈕 -->
                    <button type="submit" class="align-self-end col-md w-100 btn btn-primary font-weight-bold">送出</button>
                </form>
            </div>
            <!-- 手機板選單 -->
            <div class="top-image-form-mobile w-100 d-block d-sm-none">
                <!-- 選單 -->
                <form class="top-image-DormSearchEngine-mobile d-flex flex-row flex-wrap bg-white p-2">
                    <!-- 目的地 -->
                    <div class="form-group w-100 mb-1 postion-relative">
                        <label for="destination" class="text-primary">您要去哪裡呢?</label>
                        <input placeholder="目的地" v-model="destination" type="text" name="" id="" class="form-control p-4 SourceLight text-primary bg-white border round-circle shadow-sm">
                        <button type="button" class="position-absolute btn btn-primary circle" style="z-index:105;right:32px;top:51px;">
                            <i @click="PushToDormitoryPage" class="fas fa-search text-white"></i>
                        </button>
                    </div>
                    <!-- 更新日期按鈕 -->
                    <button @click="openDatePages" type="button" class="btn btn-primary col align-self-end font-weight-bold round-circle">日期</button>
                    <!-- 更新人數&房型按鈕 -->
                    <button @click="openOtherFilterPages" type="button" class="btn btn-primary col align-self-end font-weight-bold round-circle">人數和房型</button>
                    <!-- 日期 -->
                    <div class="top-image-DormSearchEngine-mobile-DatePages d-none sildeOut vw-100 position-fixed bg-white">
                        <i @click="openDatePages" class="fas fa-times text-primary"></i>
                        <MobileHotelDatePicker 
                            class="top-image-DormSearchEngine-mobile-Datepicker"
                            :maxDate="maxDate"
                            :startDate="startDate"
                            :endDate="endDate"
                            :placeholder="today"
                            @confirm="updateSelectDate"
                            :ParentMethod="openDatePages"
                            :mobile="'mobile'" />
                    </div>
                    <!-- 人數&房型 -->
                    <div class="top-image-DormSearchEngine-mobile-OtherFilterPages d-none sildeOut vw-100 position-fixed bg-white">
                        <i @click="openOtherFilterPages" class="align-self-end fas fa-times text-primary"></i>
                        <div class="mb-1 p-2 form-group">
                            <label for="guest" class="text-primary h4 pb-2">客人數</label>
                            <div class="d-flex flex-row align-items-center justify-content-between input-group">
                                <label for="guest" class="text-primary h5 m-0 col-3">人數</label>
                                <div class="d-flex flex-row align-items-center justify-content-between col-6 input-group">
                                    <button type="button" class="btn btn-primary text-white circle m-0" @click="updateGuest(1)">
                                        <plusSign class="plus"/>
                                    </button>
                                    <span>{{guest}}</span>
                                    <button type="button" class="btn btn-primary text-white circle m-0" @click="updateGuest(-1)">
                                        <subtractSign class="subtract"/>
                                    </button>
                                </div>
                            </div>
                        </div>
                        <div class="mb-1 p-2 form-group">
                            <label for="Dormtype" class="pb-2 text-primary h4">房型</label>
                            <select name="DormType" class="top-image-DormSearchEngine-mobile-DormTypeSlector text-primary h6" v-model="Dormtype">
                                <option value="經典雙人房">經典雙人房</option>
                                <option value="悠閒單人房">悠閒單人房</option>
                                <option value="歡樂混合房">歡樂混合房</option>
                                <option value="野生搭帳">野生搭帳</option>
                            </select>   
                        </div>
                        <button @click="openOtherFilterPages" type="button" class="btn btn-primary w-75 mt-7 mr-3 font-weight-bold float-right">送出</button>
                    </div>
                </form>
            </div>

            <img svg-inline svg-sprite src="@/assets/MainTopImage.svg" class="col-12 col-md-5 offset-md-1 mt-3 mt-md-0" />
        </div>
        <!-- section1 -->
        <div class="d-flex flex-row flex-wrap align-items-center justify-content-md-between justify-content-center col-12 mt-7">
            <!-- content -->
            <div class="d-flex flex-row justify-content-md-start order-1 order-md-1 col-lg-6 col-md-6 col-12 mt-5">
                <ul class="h6 font-weight-light">
                    <ol><h1 class="text-primary font-weight-bold">旅行累了</h1></ol>
                    <ol><h5 class="text-secondary font-weight-normal mb-5">在這裡可以尋找到甚麼</h5></ol>
                    <ol>休息是為了走更長的路，累了就停</ol>
                    <ol>下腳步讓我們替你找到舒適的住宿</ol>
                    <ol>充飽了電再出發</ol>
                </ul>
            </div>
            <!-- image -->
            <img class="col-12 col-sm-8 col-md-6 order-0 order-md-2" src="@/assets/IndexSectionCampfire.svg" alt="Booking">
        </div>
        <!-- section2 -->
        <div class="d-flex flex-row flex-wrap align-items-center justify-content-center justify-content-md-between col-12 mt-7">
            <!-- image -->
            <img class="col-md-6 col-sm-8 col-12" src="@/assets/IndexSectionBooking.svg" alt="Booking">
            <!-- content -->
            <div class="d-flex flex-row justify-content-md-end col-12 col-md-6 mt-5">
                <ul class="h6 font-weight-light">
                    <ol><h1 class="text-primary font-weight-bold">找訂房</h1></ol>
                    <ol><h5 class="mb-5 text-secondary font-weight-normal">在這裡可以尋找到甚麼</h5></ol>
                    <ol>我們提供最簡單的方式，尋找您心目中</ol>
                    <ol>最理想的住宿，四種房型任你挑選，讓</ol>
                    <ol>您在冒險的過程中，有個溫暖的避風港</ol>
                </ul>
            </div>
        </div>
        <!-- ... -->
        <div class="d-flex justify-content-center col-12 mt-8">
            <img class="relexFont col-5 col-sm-2 col-md-2" src="@/assets/sm-好好休息是不需要理由的.svg" alt="好好休息是不需要理由的">
        </div>
        <!-- card -->
        <!-- 排版使用flexbox & grid 並用 -->
        <!-- 大螢幕為2X2網格，小螢幕為1X4網格 -->
        <div class="dormType-card align-items-stretch col-12 mt-8">

            <div class="
            dormType-card-item 
            d-flex flex-md-row flex-column flex-wrap align-content-stretch align-items-center
            border-0">

                <img class="col-8 col-sm col-md-7 p-1" src="@/assets/doublebed.svg" alt="">
                <div class="d-flex flex-column justify-content-center col-md-5 ">
                    <p class="text-center text-primary font-weight-lighter">Double bed</p>
                    <div class="d-flex justify-content-center align-items-start">
                        <router-link to="/products" class="btn btn-primary text-white SourceLight">Learn More</router-link>
                    </div>
                </div>

            </div>

            <div class="
            dormType-card-item
            d-flex flex-md-row flex-column flex-wrap align-content-stretch align-items-center
            border-0">

                <img class="col-8 col-sm col-md-7 p-1" src="@/assets/bunk.svg" alt="">
                <div class="d-flex flex-column justify-content-center col-md-5">
                    <p class="text-center text-primary">Bunk bed</p>
                    <div class="d-flex justify-content-center align-items-start">
                        <router-link to="/products/mixdorm" class="btn btn-primary text-white SourceLight">Learn More</router-link>
                    </div>
                </div>

            </div>

            <div class="
            dormType-card-item 
            d-flex flex-md-row flex-column flex-wrap align-content-stretch align-items-center
            border-0">

                <img class="col-8 col-sm col-md-7 p-1" src="@/assets/tent.svg" alt="">
                <div class="d-flex flex-column justify-content-center col-md-5">
                    <p class="text-center text-primary">Tent</p>
                    <div class="d-flex justify-content-center align-items-start">
                        <router-link to="/products/tentdorm" class="btn btn-primary text-white SourceLight">Learn More</router-link>
                    </div>
                </div>

            </div>

            <div class="
            dormType-card-item
            d-flex flex-md-row flex-column flex-wrap align-content-stretch align-items-center
            border-0">

                <img class="col-8 col-sm col-md-7 p-1" src="@/assets/signalbed.svg" alt="">
                <div class="d-flex flex-column justify-content-center col-md-5">
                    <p class="text-center text-primary">Signalbed</p>
                    <div class="d-flex justify-content-center align-items-start">
                        <router-link to="/products/tentdorm" class="btn btn-primary text-white SourceLight">Learn More</router-link>
                    </div>
                </div>

            </div>

            
            
        </div>
    </div>
</template>

<script>
import $ from 'jquery';
//Vues mapGetters
import { mapGetters } from 'vuex';
//Datepicker
import VueHotelDatepicker from '@northwalker/vue-hotel-datepicker/src/components/VueHotelDatepicker.vue'
import MobileHotelDatePicker from '@/components/MobileHotelDatePicker.vue'
//symobl
import plusSign from '@/components/icon/plusSign'
import subtractSign from '@/components/icon/subtractSign'

export default {
    components:{
        VueHotelDatepicker,
        MobileHotelDatePicker,
        plusSign,
        subtractSign
    },
    computed:{
        destination:{
            get(){
                return this.$store.state.searchEngine.destination;
            },
            set(destination){
                this.$store.commit('searchEngine/DESTINATION',destination);
            }
        },
        Dormtype:{
            get(){
                return this.$store.state.searchEngine.Dormtype;
            },
            set(type){
                this.$store.commit('searchEngine/DORMTYPE',type);
            }
        },
        ...mapGetters('searchEngine',['today','maxDate','guest','startDate','endDate']),
    },
    methods: {
        updateSelectDate(data){
            this.$store.commit('searchEngine/STARTDATE',data.start);
            this.$store.commit('searchEngine/ENDDATE',data.end);
        },
        updateGuest(num){
            this.$store.dispatch('searchEngine/updateGuestNum',num)
        },
        PushToDormitoryPage(){
            this.$router.push("/dormitory")
        },
        openDatePages(){
            $('.top-image-DormSearchEngine-mobile-DatePages')
            .toggleClass('sildeOut sildeIn')
            .delay(800)
            .toggleClass('d-none')
            .css('height',`${window.outerHeight}`);
        },
        openOtherFilterPages(){
            $('.top-image-DormSearchEngine-mobile-OtherFilterPages')
            .toggleClass('sildeOut sildeIn')
            .delay(800)
            .toggleClass('d-none')
            .css('height',`${window.outerHeight}`);
        },
    }
}
</script>

<style lang="scss" scoped>
@import '@/assets/scss/_breakpoint.scss';
ul,ol{
    margin-left: 0;
    padding-left: 0;
}
@keyframes sildeIn {
    from{
        transform: translateY(0%);
        opacity:0 ;
    }
    to{
        transform: translateY(-800px);
    }
}
@keyframes sildeOut {
    from{
        transform: translateY(-800px);
    }
    to{
        transform: translateY(0%);
        opacity:0 ;
    }
}
.sildeIn{
    animation: sildeIn 0.5s cubic-bezier(0, 0, 0, 1.01);
    transform:translateY(-800px) !important; 
}
.sildeOut{
    animation: sildeOut 0.5s cubic-bezier(1.01, 0, 0, 0);
    transform:translateY(0) !important; 
}


.top-image{
    height: auto;
    @include breakpoint(xs){
        &-form-mobile{
        display: block !important;
            .fa-times{
                z-index: 120;
                font-size: 2rem;
                padding: 1rem;
                right: 0;
            }
        }
        &-DormSearchEngine-mobile{
            button{
                margin: 3px;
            }
            &-DatePages,&-OtherFilterPages{
                z-index:110;
                top:800px;
                left:0;
                transition: .5s cubic-bezier(0.075, 0.82, 0.165, 1);
            }
            .fa-times{
                font-size: 2rem;
                padding: 1rem;
                right: 0;
            }
            &-DormTypeSlector{
                -moz-appearance: none;
                -webkit-appearance: none;
                appearance: none;
                width: 100%;
                border: none !important;
                border-bottom:thin solid #3F5D45 !important;
                padding: 0.375rem 1.75rem 0.8rem 0.75rem;
                &::-ms-expand{
                    display: none;
                }
                &:focus{
                    outline-offset: 0 !important;
                    outline:none;
                }
            }
            &-Datepicker{
                width: 100%;
                height: 420px;
            }
        }
    }
}
@include breakpoint(xs){
    .relexFont{
        height: 400px;
    }
}
.dormType-card{
    display: grid;
    grid-template-columns: repeat(6,1fr);
    grid-template-rows: repeat(2,minmax(100px,1fr));
    grid-auto-flow:column;
    width: 100%;
    grid-gap: 5px;
    @include breakpoint(xs){
        grid-template-columns: repeat(6,1fr);
        grid-template-rows: repeat(3,minmax(100px,1fr));
        grid-gap: 15px;
    }
    &-item{
        transition:.2s ease-out;
        &:nth-child(1){
            grid-column-start: 1;
            grid-column-end: span 3;
            grid-row-start: 1;
            grid-row-end: span 1;
            @include breakpoint(xs){
                grid-column-start: 1;
                grid-column-end: span 10;
                grid-row-start: 1;
                grid-row-end: span 1;
            }
        }
        &:nth-child(2){
            grid-column-start: 1;
            grid-column-end: span 3;
            grid-row-start: 2;
            grid-row-end: span 1;
            @include breakpoint(xs){
                grid-column-start: 1;
                grid-column-end: span 10;
                grid-row-start: 2;
                grid-row-end: span 1;
            }
        }
        &:nth-child(3){
            grid-column-start: 4;
            grid-column-end: span 3;
            grid-row-start: 1;
            grid-row-end: span 1;
            @include breakpoint(xs){
                grid-column-start: 1;
                grid-column-end: span 10;
                grid-row-start: 3;
                grid-row-end: span 1;
            }
        }
        &:nth-child(4){
            grid-column-start: 4;
            grid-column-end: span 3;
            grid-row-start: 2;
            grid-row-end: span 1;
            @include breakpoint(xs){
                grid-column-start: 1;
                grid-column-end: span 10;
                grid-row-start: 4;
                grid-row-end: span 1;
            }
        }
        &:hover{
            transform: scale(1.1,1.1)
        }
    }
}
</style>



