<template>
        <form class="logIn-form d-flex flex-column position-relative col-12 col-sm-9 col-md-7 col-lg-5 p-3 mt-5 ml-auto mr-auto bg-success border rounded" @submit.prevent="signin">
            <h1 class="logIn-form-title text-primary text-center font-weight-bold">會員登入</h1>
            <h4 v-if="login === false" class="logIn-form-fail text-danger text-center font-weight-bold">登入失敗</h4>
            <div class="logIn-form-account d-flex w-100 mt-6 mb-0 form-group">
                <div class="w-100">
                    <label class="sr-only" for="inlineFormInputGroup">Username</label>
                    <div class="input-group mb-2">
                        <div class="input-group-prepend">
                        <div class="input-group-text"><i class="fas fa-user text-primary"></i></div>
                        </div>
                        <input v-model="user.username" aria-describedby="account" placeholder=" 帳號為ogcing78@gmail.com" type="email" class="p-4 form-control text-primary">
                    </div>
                </div>
            </div>
            <div class="logIn-form-password d-flex w-100 mt-1 form-group">
                <div class="w-100">
                    <label class="sr-only" for="inlineFormInputGroup">Username</label>
                    <div class="input-group mb-2">
                        <div class="input-group-prepend">
                        <div class="input-group-text"><i class="fas fa-key text-primary"></i></div>
                        </div>
                        <input id="password" v-model="user.password" placeholder=" 密碼為ogcing7890" type="password" class="p-4 form-control text-primary">
                    </div>
                </div>
            </div>
            <div class="logIn-form-savePassword ml-3 form-group">
                <input type="checkbox" class="form-check-input" id="SavePassword">
                <label class="form-check-label text-primary font-weight-bold" for="SavePassword">記住我</label>
            </div>
            <button type="submit" class="logIn-form-btn w-100 p-2 btn btn-primary font-weight-bold">帳號登入</button>
        </form>
</template>

<script>
export default {
    data () {
    return {
      user:{
          username:'',
          password:'',
      },
      message:'',
      login:''
    };
  },
  methods: {
      signin(){
        const api = `${process.env.APIPATH}/admin/signin`;
        const vm = this;
        this.$http.post(api,vm.user).then((response) => {
            console.log(response.data);
            if(response.data.success){
                vm.$router.push("/index");
                vm.$router.msg = '已登入';
                vm.login = true;
                vm.loginRes(response.data.success);
            }else{
                vm.$router.msg = '登入失敗';
                vm.message = '登入失敗';
                vm.login = false;
                vm.loginRes(response.data.success);
            }
        });
      },
      loginRes(res){
          this.$bus.$emit('loginRes:push',res);
      }
  },
}
</script>

<style lang='scss' scoped>
@import '@/assets/scss/_breakpoint.scss';
$input-height: 56.8px;


.logIn-form{
    &-password::placeholder,&-account::placeholder{
        color: #336633 !important;
    }
    @include breakpoint(xs){
        height: 100vh;
        margin-top: 0 !important;
        border:none !important;
        background-color: #fff !important;
        &-btn{
            margin-top: 10px;
        }
    }
}
</style>



