//moment.js 處理日期
import moment from 'moment';

export default{
    strict:true,
    namespaced:true,
    state:{
        destination:'',
        today: moment().format('YYYY/MM/DD'),
        maxDate: moment().add(90,'days').format('YYYY/MM/DD'),
        startDate:undefined,
        endDate:undefined,
        Dormtype:'經典雙人房',
        guest:1
    },
    actions:{
        updateGuestNum(context,payload){
            context.commit('GUEST',payload)
        }
    },
    mutations:{
        DESTINATION(state,payload){
            state.destination = payload;
        },
        STARTDATE(state,payload){
            state.startDate = payload;
        },
        ENDDATE(state,payload){
            state.endDate = payload;
        },
        DORMTYPE(state,payload){
            state.Dormtype = payload;
        },
        GUEST(state,payload){
            if(state.guest === 1 && payload === -1){
                state.guest = 1;
                return;
            };
            state.guest += Number(payload);
        },
    },
    getters:{
        today:state => state.today,
        maxDate:state => state.maxDate,
        guest:state => state.guest,
        startDate:state => state.startDate,
        endDate:state => state.endDate
    }
}


