                <h6 class="TempProduct-bookIngPages-items-text float-left p-0 text-right text-primary">{{TempProduct.origin_price}} x {{ DateCompute }} 晚 x {{guest}} 人</h6>
                <h6 class="TempProduct-bookIngPages-items-text float-right p-0 text-right text-primary">$ {{TempProduct.origin_price * guest * DateCompute}} TWD </h6>
            </div>
            <!-- 清潔費 -->
            <div class="p-3 form-group SourceLight">
                <h6 class="TempProduct-bookIngPages-items-text float-left p-0 text-right text-primary">清潔費 10%</h6>
                <h6 class="TempProduct-bookIngPages-items-text float-right p-0 text-right text-primary">$ {{ Math.floor(TempProduct.origin_price * 0.1) }} TWD </h6>
            </div>
            <hr style="width:90%">
            <!-- 總價 -->
            <div class="p-4 form-group">
                <h4 class="p-0 text-right float-left text-primary font-weight-bold">總價</h4>
                <h4 class="p-0 text-right float-right text-primary font-weight-bold">$ {{ totalPrice }} TWD </h4>
            </div>
            <!-- 表單送出按鈕 -->
