        </form>

        <!-- 手機的 日曆視窗 -->
        <div class="TempProduct-datePage vw-100 position-fixed bg-white d-none sildeOut">
            <!-- 關閉按鈕 -->
            <i @click="openDatePages" class="fas fa-times text-primary" aria-label="close button"></i>
            <!-- 日曆元件 -->
            <MobileHotelDatePicker 
                class="TempProduct-datePage-DatePicker"
                :maxDate="maxDate"
                :startDate="startDate"
                :endDate="endDate"
                :placeholder="today"
                @confirm="updateSelectDate"
                :ParentMethod="openDatePages"
                :mobile="'mobile'" />
        </div>

        <!-- 手機的 預定視窗 -->
        <form @submit.prevent="form.submit();CheckOut()" class="TempProduct-bookIngForm vw-100 position-fixed bg-white d-none sildeOut ml-n1 p-3">
            <!-- 關閉按鈕 -->
            <i @click="openbookIngForm();openbookIngPages()" class="mb-1 fas fa-times text-primary" aria-label="close button"></i>
            <h3 class="mb-1 text-primary font-weight-bold">個人資料</h3>
            <div class="mt-5 mt-md-4 mt-lg-5">
                <vuex-text label="暱稱"
                        name="user.data.name"
                        class="valid-input text-primary"
                        placeholder="Golden"
                        v-model="form.user.data.name"
                        :errors="form.errors.get('user.data.name')"
                        @event="form.listen($event)"
                        required
                ></vuex-text>

                <vuex-text label="連絡電話"
                        name="user.data.tel"
                        type="tel"
                        mask="(##) ####-####"
                        placeholder="(09) 0000-0008"
                        class="valid-input text-primary"
                        v-model="form.user.data.tel"
                        :errors="form.errors.get('user.data.tel')"
                        @event="form.listen($event)"
                        required
                ></vuex-text>

                <vuex-text label="Email"
                        name="user.data.email"
                        type="email"
                        placeholder="Example@gmail.com"
                        class="valid-input text-primary"
                        v-model="form.user.data.email"
                        :errors="form.errors.get('user.data.email')"
                        @event="form.listen($event)"
                        required
                ></vuex-text>

                <vuex-text label="Message"
                        name="message"
                        type="textarea"
                        class="valid-input text-primary"
                        v-model="form.message"
                        :errors="form.errors.get('message')"
                        @event="form.listen($event)"
                ></vuex-text>
            </div>
            <div class="text-primary font-weight-normal text-right">訂單摘要</div>
            <div class="form-group p-1">
                <h6 class="float-left p-0 text-right text-primary SourceLight">小記</h6>
                <h6 class="float-right p-0 text-right text-primary SourceLight">{{totalPrice}}</h6>
            </div>
            <div class="form-group p-1">
                <h6 class="float-left p-0 text-left text-primary SourceLight">平台費</h6>
                <h6 class="float-right p-0 text-right text-primary SourceLight">{{planformfee}}</h6>
            </div>
            <div class="TempProduct-bookIngForm-totalPrice p-1 pt-2 mt-1 form-group">
                <h4 class="float-left p-0 text-left text-primary">總價</h4>
                <h4 class="float-right p-0 text-right text-primary">{{ totalPrice + planformfee }}</h4>
            </div>
            
            <button type="submit" class="TempProduct-bookIngForm-submit-btn w-100 mt-5 btn btn-primary text-white font-weight-normal">確認</button>
        </form>

