<script>
//JQ
import $ from'jquery';
//Moment Js 處理時間
import moment from 'moment';
import { extendMoment } from 'moment-range';
//示意地圖用
import GoogleMap from '@/components/GoogleMap';
//Vues mapGetters
import { mapGetters } from 'vuex';
//Datepicker
import VueHotelDatepicker from '@northwalker/vue-hotel-datepicker/src/components/VueHotelDatepicker.vue'
import MobileHotelDatePicker from '@/components/MobileHotelDatePicker.vue'
//symobl
import plusSign from '@/components/icon/plusSign'
import subtractSign from '@/components/icon/subtractSign'
//vuex-form 表單驗證
import { Form } from "vuex-forms";
//表單最小長度限制與不得為空
import { required, email , minLength } from "vuex-forms/src/validators";

export default {
    data() {
        return {
            EveryDormStayDays:[],
            disableDays:[],
            form: new Form(this, {
                message:'',
                user:{
                    data:{
                        name: '',
                        tel:'',
                        email:''
                    }
                }
            }, {
                validations: {
                    message: {},
                    user:{
                        data:{
                            name: { required },
                            tel:{required , minLength:minLength(10)},
                            email:{required , email}
                        }
                    }
                }
            })
        };
    },
    components:{
        GoogleMap,
        VueHotelDatepicker,
        MobileHotelDatePicker,
        plusSign,
        subtractSign
    },
    computed:{
        totalPrice(){
            return Math.floor((this.TempProduct.origin_price * this.guest * this.DateCompute) + (this.TempProduct.origin_price * 0.1));
        },
        DateCompute(){
            var day = moment.duration(moment(this.endDate, 'YYYY/MM/DD').diff(moment(this.startDate, 'YYYY/MM/DD'))).asDays();
            if(day <= 0 || isNaN(day)){
                day = 1;
                return day;
            }
            return day;
        },
        isLike(){
            //心願單為空時，isLike全都為否
            //心願單有此產品時才回true，否則回false
                if(this.like.length === 0) return false;
                let elementPos = Array.from(this.like).map(function(x) {return x['product_id']; }).indexOf(this.productId);
                return elementPos === -1 ? false : this.like[elementPos].isLike;
        },
        ...mapGetters(['isLoading']),
        ...mapGetters('cartModules',['like','planformfee']),
        ...mapGetters('productModules',['productId','TempProduct','TempPosition']),
        ...mapGetters('searchEngine',['today','maxDate','guest','startDate','endDate']),
    },
    methods:{
        updateGuest(num){
            this.$store.dispatch('searchEngine/updateGuestNum',num)
        },
        updateSelectDate(data){
            this.$store.commit('searchEngine/STARTDATE',data.start);
            this.$store.commit('searchEngine/ENDDATE',data.end);
        },
        addtolike(id){
            //依單一項目喜好值決定刪除或新增
            if(this.isLike){
                let elementPos = Array.from(this.like).map(function(x) {return x['product_id']; }).indexOf(id); //單一項目引索值
                this.$store.commit('cartModules/ISLIKE',{ elementPos , boolean:false }) //更新喜好值，改變按讚icon顏色
                this.$store.dispatch('cartModules/DelLike',this.productId) //刪除心願
            }else{
                //endDate 和 startDate 不能為undefined，所以默認值設為今天與明天
                const endDate = this.endDate === undefined ?　moment().add(1,'days').format('YYYY/MM/DD') : this.endDate;
                const startDate = this.startDate === undefined ?　this.today : this.startDate;
                //新增心願
                this.$store.dispatch('cartModules/addToLike',{ 
                    startDate:startDate,
                    endDate:endDate,
                    product_id:this.productId,
                    product : this.TempProduct,
                    Price : this.totalPrice,
                    originPrice : this.totalPrice,
                    qty:this.DateCompute,
                    isLike:this.isLike
                })
                
                let elementPos = Array.from(this.like).map(function(x) {return x['product_id']; }).indexOf(id); //單一項目引索值
                this.$store.commit('cartModules/ISLIKE',{ elementPos , boolean:true }); //更新喜好值，改變按讚icon顏色
                localStorage.setItem('LIKE', JSON.stringify(this.like)); //存到localStorage
            }
        },
        goToCheckOutDesktop(){
            if(this.startDate && this.endDate){
                this.openbookIngFormCheckOut();
            }else{
                this.$bus.$emit('messsage:push','日期為空','warning');
            }
        },
        goToCheckOut(){
            if(this.startDate && this.endDate){
                this.openbookIngForm();
            }else{
                this.$bus.$emit('messsage:push','日期為空','warning');
            }
        },
        async CheckOut(){
            if(this.startDate && this.endDate){
                const cart = { 
                        startDate:this.startDate,
                        endDate:this.endDate,
                        product_id:this.productId,
                        product : this.TempProduct,
                        Price : this.totalPrice,
                        originPrice : this.totalPrice,
                        qty:this.DateCompute,
                        isLike:this.isLike
                }

                await this.$store.dispatch('cartModules/addtocart',cart);

                const order = {
                    message:this.form['_data'].message,
                    user:{
                        name: this.form['_data'].user.data.name,
                        email: this.form['_data'].user.data.email,
                        tel: this.form['_data'].user.data.tel,
                        address: "Tainan"
                    }
                }
                this.$store.dispatch('orderModules/orderCheckOut', order);
            }else{
                this.$bus.$emit('messsage:push','日期為空','warning');
            }
        },
        ModalOpen(){
            $('#productModal').modal('show');
        },
        checkOutModalOpen(){
            if(this.startDate && this.endDate){
                $('.TempProduct-checkOut-modal').modal('show');
            }else{
                this.$bus.$emit('messsage:push','日期為空','warning');
            }
        },
        openImagePages(){
            $('.TempProduct-imagePages')
            .toggleClass('sildeOut sildeIn')
            .delay(800)
            .toggleClass('d-none')
            .css('height','100vh');
        },
        openbookIngPages(){
            $('.TempProduct-bookIngPages')
            .toggleClass('sildeOut sildeIn')
            .delay(800)
            .toggleClass('d-none')
            .css('height','100vh');
        },
        openDatePages(){
            $('.TempProduct-datePage')
            .toggleClass('sildeOut sildeIn')
            .delay(800)
            .toggleClass('d-none')
            .css('height',`${window.outerHeight}`);
        },
        openbookIngForm(){
            $('.TempProduct-bookIngForm')
            .toggleClass('sildeOut sildeIn')
            .delay(800)
            .toggleClass('d-none')
            .css('height',`${window.outerHeight}`);
        }
    },
    created(){
        this.$store.commit('productModules/PRODUCTID',this.$route.params.id); //單一項目產品ID
        this.$store.dispatch('productModules/getTempProduct'); //取得單一項目產品
        this.$store.dispatch('cartModules/getLike'); //取得心願單
    },
}
</script>

<style lang="scss" scoped>
@import '@/assets/scss/_breakpoint.scss';
$topimage-height: 400px;

@keyframes sildeIn {
    from{
        transform: translateY(0%);
        opacity:0 ;
    }
    to{
        transform: translateY(-800px);
    }
}
@keyframes sildeOut {
    from{
        transform: translateY(-800px);
    }
    to{
        transform: translateY(0%);
        opacity:0 ;
    }
}
.sildeIn{
    animation: sildeIn 0.5s cubic-bezier(0, 0, 0, 1.01);
    transform:translateY(-800px) !important; 
}
.sildeOut{
    animation: sildeOut 0.5s cubic-bezier(1.01, 0, 0, 0);
    transform:translateY(0) !important; 
}
.like-btn:focus{
    box-shadow: none;
    -webkit-box-shadow:none;
}

.TempProduct{
    min-height: 868px;
    &-topImage{
        &-img{
            height: $topimage-height;
            @include breakpoint(xs){
                height: $topimage-height * 0.7;
            }
        }
        &-button{
            display: none !important;
            @include breakpoint(xs){
                display: block !important;
                bottom: 0;
                right: 0;
                z-index: 8;
            }
        }
    }
    &-imagePages{
        @include breakpoint(xs){
            z-index: 11;
            top: 800px;
            overflow-y: scroll;
            &-img{
                height: calc(100vh/2.5);
            }
            .fa-times{
                z-index: 120;
                font-size: 2rem;
                padding: 1rem;
                right: 0;
            }
        }
    }
    /deep/ &-Datepicker{
        input{
        min-width: 100% !important;
        padding-top: 3px !important;
        padding-bottom: 3px !important;
        background-color:rgb(255, 255, 255);
        color:#336633 !important;
        }
        .vhd-picker{
            min-height: auto !important;
        }
    }
    &-form-desktop{
        display: flex;
        @include breakpoint(xs){
            display: none !important;
        }
        &-items-text{
            @include breakpoint(xs){
                font-size: 95%;
            }
        }
    }
    &-bottom-nav{
        display: none;
        @include breakpoint(xs){
            display: block;
            left:0;
            bottom:0px;
            z-index:10;
            box-shadow: 0 0 10px #00000024;
            .fa-star{
                font-size: 10px;
            }
        }
    }
    &-bookIngPages{
        @include breakpoint(xs){
            z-index: 11;
            top: 800px;
            overflow-y: scroll;
            &-img{
                min-height: 100px;
            }
            .fa-times{
                z-index: 11;
                font-size: 2rem;
                padding: 1rem;
                right: 0;
            }
        }
    }
    /deep/ &-datePage{
        @include breakpoint(xs){
            z-index:12;
            top:800px;
            left:0;
            transition: .5s cubic-bezier(0.075, 0.82, 0.165, 1);
            .fa-times{
                z-index: 12;
                font-size: 2rem;
                padding: 1rem;
                right: 0;
            }
            /deep/ &-DatePicker{
                width: 100vw;
                height: 420px;
            }
        } 
    }
    .TempProduct-bookIngForm{
        @include breakpoint(xs){
            z-index: 15;
            top: 800px;
            right: 0;
            overflow-y: scroll;
            &-totalPrice{
                margin-top: 36px !important;
                border-top: 1px solid #336633 !important;
            }
            .fa-times{
                z-index: 15;
                font-size: 2rem;
                right: 0;
            }
            /deep/ .form-errors{
                color: #dc3545;
            }
            /deep/ .valid-input > input{
                border-radius: 50px;
            }
        }
    }
    &-checkOut-modal{
        .modal-dialog{
            max-width: 50%;
        }
        &-img{
            height: 140px;
        }
    }
}


</style>



