import axios from 'axios'


export default{
    strict:true,
    namespaced:true,
    state:{
        order:[]
    },
    actions:{
        async orderCheckOut(context,payload){
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
            const order = payload
            await axios.post(url,{data:order}).then((response) =>{
                if(response.data.success){
                    console.log('orderCheckOut成功',response.data)
                }else{
                    console.log('orderCheckOut失敗',response.data)
                }
            });
        },
    },
    mutations:{
        ORDER(state,payload){

        }
    },
    getters:{
        form: state => state.form,
    }
}


