<template>
        <footer class="container-fluid mt-5 p-0">
            <div class="footer-background d-flex flex-column justify-content-center align-items-center p-lg-7 p-md-5 p-5 position-relative">
                <img src="@/assets/logo-all-yellow.svg" alt="Sweetast">
                <h1 class="text-light text-center mb-4 text-shadow">Subscribe To Our Newsletter</h1>
                <div class="input-group w-50">
                    <input type="text" class="form-control p-1 p-md-4 shadow" placeholder="example@gmail.com" aria-label="Recipient's username" aria-describedby="button-addon2">
                    <div class="input-group-append">
                        <button @click="showThank" type="button" class="btn btn-secondary" id="button-addon2">Subscribe</button>
                    </div>
                </div>
            </div>
        </footer>
</template>

<script>
export default {
    methods:{
        showThank(){
            this.$bus.$emit('messsage:push', '感謝訂閱','primary')
        }
    }
}
</script>

<style lang="scss" scoped>
@import url('https://fonts.googleapis.com/css?family=Titillium+Web:700&display=swap');
.footer-background{
    font-family: 'Titillium Web', sans-serif;
    background-image: url('../assets/footer.svg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    height: auto;
    img{
            width: 30%;
            margin-bottom: 20px;
        }
}
@media only screen and (max-width: 1400px) {
    .footer-background{
        img{
            width: 10%;
            margin-bottom: 20px;
        }
    }
}
@media only screen and (max-width: 992px) {
    .footer-background{
        img{
            width: 50%;
            margin-bottom: 20px;
        }
    }
}
@media only screen and (max-width: 768px) {
    .footer-background{
        img{
            width: 60%;
            margin-bottom: 20px;
        }
    }
}
@media only screen and (max-width: 576px) {
    footer{
        display: none;
    }
    .footer-background{
        img{
            width: 60%;
            margin-bottom: 40px;
        }
    }
}
@media only screen and (max-width: 480px) {
    footer{
        display: none;
    }
    .footer-background{
        img{
            width: 75%;
            margin-bottom: 20px;
        }
        h1{
            font-size: 2rem;
        }
        .input-group{
            width: 75% !important;
        }
    }
}
</style>



