import axios from 'axios'

export default{
    strict:true,
    namespaced:true,
    state:{
        totalPage:1,
        products:[],
        position:[],
        TempProduct:[],
        TempPosition:{},
        productId:'',
        signalProduct:[],
        mapCenter: {lat:25.045898,lng:121.53874}
    },
    actions:{
        //因為api有分頁數一次只能抓10筆,所以先計算完頁數
        //再透過for迴圈一次抓全部
        async getTotalPage(context) {
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
            context.commit('LOADING',true,{root:true});
            await axios.get(url).then((response) => {
                context.commit('TOTALPAGE',response.data.pagination['total_pages'])
            });
        },
        //所有房間(產品)
        async getProducts(context){
            //讀取頁數
            await context.dispatch('getTotalPage');
            let AllTheProducts = [];
            for(let i = 1;i<=context.state.totalPage;i++){
                const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${i}`
                await axios.get(url).then((response)=>{
                    AllTheProducts.push(...response.data.products);
                })
            }
            context.commit('LOADING',false,{root:true});
            context.commit('PRODUCTS', AllTheProducts)
        },
        //產品列頁地圖地標
        getPosition(context,filterProducts){
            //如果filterProducts為空或為undefined，默認為[]
            //避免filterProducts讀取太慢
            if(filterProducts === undefined || filterProducts == [] || filterProducts === null || filterProducts.length == 0) {
                context.commit('POSITION',[])
                return;
            };
            //push所有住宿店家位置與地圖資訊視窗html
            const position = [];
            filterProducts.forEach(element => {
                if(element.address.autocompleteText){
                    position.push({
                        positionPoint:{
                            lat:element.address.latitude,
                            lng:element.address.longitude
                        },
                        infoText:`
                        <div class="productInfoWindow d-flex flex-column">
                            <div id="carouselExampleControls${element.id}" class="carouselExampleControls carousel slide col-12 p-0 mb-1" style="width:260px;max-width:444px;height:186px;max-height:300px" data-ride="carousel" data-interval="false">
                                <div class="carousel-inner rounded w-100 h-100">
                                    <!-- 圖一 -->
                                    <div class="carousel-item w-100 h-100 active overflow-hidden">
                                        <div class="d-block w-100 h-100" style="background-image:url(${element.imageUrl});background-position: center;background-size: 100% 100%;"></div>
                                    </div>
                                    <!-- 圖二 -->
                                    <div class="carousel-item w-100 h-100 overflow-hidden">
                                        <div class="d-block w-100 h-100" style="background-image:url(${element.imageUrl1});background-position: center;background-size: 100% 100%;"></div>
                                    </div>
                                    <!-- 圖三 -->
                                    <div class="carousel-item w-100 h-100 overflow-hidden">
                                        <div class="d-block w-100 h-100" style="background-image:url(${element.imageUrl2});background-position: center;background-size: 100% 100%;"></div>
                                    </div>
                                </div>
                                <!-- 換頁按鈕-左 -->
                                <a class="carousel-control-prev" href="#carouselExampleControls${element.id}" role="button" data-slide="prev">
                                    <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                                    <span class="sr-only">Previous</span>
                                </a>
                                <!-- 換頁按鈕-右 -->
                                <a class="carousel-control-next" href="#carouselExampleControls${element.id}" role="button" data-slide="next">
                                    <span class="carousel-control-next-icon" aria-hidden="true"></span>
                                    <span class="sr-only">Next</span>
                                </a>
                            </div>
                            <span class="SourceLight text-darkgray" style="font-size:95%">${element.content}</span>
                            <a href="#/tempproduct/${element.id}" class="mapMaker-dorm-title h5 text-primary">${element.title}</a>
                            <div class="text-darkgray">${element.origin_price} TWD / night</div>
                        </div>`
                    })
                }
            });
            //更新所有位置
            context.commit('POSITION',position)
        },
        updateMapCenter(context){
            //如果position為空或為undefined，默認為{ lat:25.0392 , lng:121.525 }
            //避免position讀取太慢
            if(context.state.position == [] || context.state.position == undefined || context.state.position.length == 0){
                context.commit('MAPCENTER',{
                        lat:25.0392,
                        lng:121.525
                });
                return;
            }
            //所有產品的第一個當大地圖中心位置
            context.commit('MAPCENTER',context.state.position[0].positionPoint);
        },
        //TempProduct.vue
        //取得單一項目
        async getTempProduct(context){
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${context.state.productId}`;
            context.commit('LOADING',true,{root:true});
            await axios.get(url).then((response) =>{
                context.commit('TEMPPRODUCT',response.data.product);
                //產品沒有登入住宿地址時，則默認為{ latitude : 121.30 , longitude : 25.02 }
                if(response.data.product.hasOwnProperty('address')){
                    context.commit('LOADING',false,{root:true});
                    context.commit('TEMPPOSITION',{
                        latitude : response.data.product.address.latitude,
                        longitude : response.data.product.address.longitude
                    });
                }else{
                    context.commit('LOADING',false,{root:true});
                    context.commit('TEMPPOSITION',{
                        latitude : 121.30,
                        longitude : 25.02
                    });
                }
            });
        },
    },
    mutations:{
        //所有產品
        PRODUCTS(state,payload){
            state.products = payload;
        },
        //單一產品
        TEMPPRODUCT(state,payload){
            state.TempProduct = payload;
        },
        //單一產品位置
        TEMPPOSITION(state,payload){
            state.TempPosition = payload;
        },
        //單一產品ID
        PRODUCTID(state,payload){
            state.productId = payload;
        },
        //單一產品總價
        TOTALPAGE(state,payload){
            state.totalPage = payload;
        },
        //所有產品位置
        POSITION(state,payload){
            state.position = payload;
        },
        //大地圖中心位置
        MAPCENTER(state,payload){
            state.mapCenter = payload;
        }
    },
    getters:{
        products: state => state.products,
        productId: state => state.productId,
        TempProduct: state => state.TempProduct,
        TempPosition: state => state.TempPosition,
        totalPage: state => state.totalPage,
        position:　state => state.position,
        mapCenter: state => state.mapCenter
    }
}


